.list-menu-lateral{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    width: 317px;
    height: 750px;
    background-color: #f2f4f5;
    padding: 30px;
    box-shadow: 60px 0px 0px rgba(32, 35, 41, .8);
}

.list-close{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 65px;
    right: -40px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: none;
    box-shadow: 3px 0px 7px hsl(193, 94%, 68%, .8);
  
}

.list-close-icon{
    width: 20px;
    height: 20px;
}

.list-search{
    padding: 5px;
    width: 257px;
    height: 36px;
    border-radius: 9999px;
    background-color: #ffffff;
    border: 1px solid #d9d9d9;
    margin-bottom: 30px
}

.list-list{
    list-style: none;
    display: grid;
    gap: 10px;
}

.list-item{
    display: flex;
    width: 257px;
    height: 36px;
    border: none;
    border-radius: 8px;
    background-color: #ffffff;
    font-weight: 500;
    align-items: center;
}

.list-item p{
    padding: 5px;
}

.list-item .item-icon{
    width: 16px;
    height: 15px;
    margin: 5px;
}